
import { Options, Vue } from 'vue-class-component'
import { UserOutlined, LockOutlined } from '@ant-design/icons-vue'
import { ValidateErrorEntity } from 'ant-design-vue/es/form/interface'
import axiosUtils from '@/utils/AxiosUtils'
import { ResponseEntity } from '@/types/ResponseEntity'
interface UserAuth {
    username: string;
    password: string;
}
interface LoginUser {
    id: string;
    name: string;
    deptId: string;
    deptName: string;
    groupId: string;
    groupName: string;
}
@Options({
    components: {
        UserOutlined,
        LockOutlined
    }
})
export default class Login extends Vue {
    // 表单数据
    formData: UserAuth = {
      username: '',
      password: ''
    }

    // 认证通过登录
    async handleFinish(values: UserAuth) {
      console.log(values, this.formData)
      const params = `username=${this.formData.username}&password=${this.formData.password}`
      const result = await axiosUtils.post<ResponseEntity<LoginUser>>('/security/auth', params, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
      })
      if (result.code === 0) {
          this.$toast('登录成功')
          this.$router.push('/client')
      } else {
          this.$toast(result.msg)
      }
    }

    handleFinishFailed(errors: ValidateErrorEntity<UserAuth>) {
      console.log(errors)
    }
}
